











































import { Component, Vue, Prop, Emit  } from 'vue-property-decorator';
import CommonMixins from '@/utils/mixins/commonMixins';
import { STATUS_NAME } from '@/config/config';

// 声明引入的组件
@Component({
  name: 'ServiceList',
})
// 类方式声明当前组件
export default class ServiceList extends CommonMixins {
  @Prop({ type: Array, default: () => [] })
  private tableData: any[];

  /**
   * @description 获取订单状态
   * @params status 状态枚举
   * @returns string
   * @author chenmo
   */
  private getOrderStatusName(status: number) {
    return STATUS_NAME[status];
  }

  /**
   * @description 跳转到详情
   * @params servicePackageId id
   * @returns string
   * @author chenmo
   */
  private pushDetile(servicePackageId: string) {
    this.$router.push(`/serviceDetile?servicePackageId=${servicePackageId}`);
  }

}
